import React, { useEffect, useState } from "react";
import { connect } from "react-redux";

import { COLUNMS_HISTORY_LOTTO_1, COLUNMS_HISTORY_LOTTO_2 } from "config/table";
import { LayoutLottery, TitleBox } from "components";
import { MyLottrey } from "moduls";
import "./style.scss";

const COLUNMS_LOTTO = [
  {
    title: "종류",
    dataIndex: "typeGame",
    key: "typeGame",
    render: (text) => <div className="text-left">{text}</div>,
    align: "right",
  },
  ...COLUNMS_HISTORY_LOTTO_1,
  {
    title: "번호",
    dataIndex: "exacly",
    key: "exacly",
    align: "center",
    render: (arr) => (
      <>
        {(arr || []).map((__i, ind) => {
          return (
            <span key={`______key-exacly-${ind}`}>
              <span className={__i?.isExacly ? "bold" : ""}>
                {__i?.number || ""}
              </span>
              {__i?.leg == ind ? "" : `, `}
            </span>
          );
        })}
      </>
    ),
  },
  ...COLUNMS_HISTORY_LOTTO_2,
];
const Lottrey = ({ count, InquireLotto, getMyInquireLotto }) => {
  const onGetDataCh = async (payload = {}) => {
    await getMyInquireLotto(payload);
  };
  return (
    <LayoutLottery>
      <div id="my-lottrey" className=" container-cst mt-5 pt-5">
        <div className="____content-lottery">
          <TitleBox name="마이페이지" />
          <MyLottrey
            columns={COLUNMS_LOTTO}
            onGetDataChange={onGetDataCh}
            data={InquireLotto}
            count={count}
          />
        </div>
      </div>
    </LayoutLottery>
  );
};

export default connect(
  (state) => {
    const { history } = state;

    return {
      InquireLotto:
        (state.whitelistedTypeGames || state.whitelistedTypes
          ? history?.InquireLotto.filter(
              ({ typeGame, type }) =>
                (state.whitelistedTypeGames?.has(typeGame) ?? true) &&
                (state.whitelistedTypes?.has(type) ?? true)
            )
          : history?.InquireLotto) || [],
      count: history?.count || 0,
    };
  },
  ({ history: { getMyInquireLotto } }) => {
    return {
      getMyInquireLotto,
    };
  }
)(Lottrey);
